<template>
  <div id="bookdearch">
    <el-form :inline="true">
      <el-form-item label="キーワード">
        <el-input v-model="keyword" size="medium"></el-input>
      </el-form-item>
      <el-button type="primary" @click="onsubmit">検索</el-button>
    </el-form>
    <BookInfo v-for="(b,i) in books" :book="b" :index="i + 1" :key="b.id" :linkable="true"></BookInfo>
  </div>
</template>
<script>
import BookInfo from "../components/BookInfo";

export default {
  name: "BookSearch",
  data() {
    return {
      keyword: "vue",
      books: []
    }
  },
  components:{
      BookInfo
  },
  methods: {
    onsubmit: function() {
      this.$http(
        "https://www.googleapis.com/books/v1/volumes?q=" + this.keyword
      )
        .then(response => {
          return response.json();
        })
        .then(data => {
          this.books = [];
          data.items.forEach((b) => {
            let authors = b.volumeInfo.authors;
            let price = b.saleInfo.listPrice;
            let img = b.volumeInfo.imageLinks;
            this.books.push({
              id: b.id,
              title: b.volumeInfo.title,
              author: authors ? authors.join(",") : "",
              price: price ? price.amount : "-",
              publisher: b.volumeInfo.publisher,
              published: b.volumeInfo.publishedDate,
              image: img ? img.smallThumbnail : ""      
            })      
          });
        });
    }
  }
};
</script>